@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800");
html,
body {
  height: 100%;
  cursor: none;
  /* overflow-x: hidden; */
}
.cursor-dot,
.cursor-dot-outline {
  z-index: 800;
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;
}
.cursor-dot {
  width: 8px;
  height: 8px;
  background-color: blue;
}

.cursor-dot-outline {
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 255, 0.5);
}
.banner-title {
  transform: translateZ(35px);
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  margin-bottom: 24px;
  text-align: left;
}

.banner-subtitle {
  transform: translateZ(25px);
  font-weight: 300;
  font-size: 40px;
  line-height: 47px;
  text-align: left;
}
.banner-container {
  margin: 296px 0;
  width: 100%;
  display: flex;
  justify-content: flex-start;
}
.banner {
  background: #fff;
  border-radius: 15px;
  display: inline-block;
  perspective: 1800px;
  text-align: center;
  transform-origin: 50% 50%;
  transform-style: preserve-3d;
  transform: rotateX(11deg) rotateY(16.5deg);
  min-width: 800px;
}
.fixed-container {
  position: relative;
}
.wrapper-canvas {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#c {
  position: fixed;
  width: 100%;
  height: 100%;
  display: block;
  z-index: -1;
  bottom: -80px;
}

.loading {
  position: fixed;
  z-index: 50;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #f1f1f1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader {
  -webkit-perspective: 120px;
  -moz-perspective: 120px;
  -ms-perspective: 120px;
  perspective: 120px;
  width: 100px;
  height: 100px;
  position: fixed;
  right: 120px;
}

.loader:before {
  content: "";
  position: absolute;
  left: 25px;
  top: 25px;
  width: 50px;
  height: 50px;
  background-color: #9bffaf;
  animation: flip 1s infinite;
}

@keyframes flip {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotateY(180deg);
  }

  100% {
    transform: rotateY(180deg) rotateX(180deg);
  }
}

/*About me*/
.about {
  max-width: 800px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.about__title {
  font-weight: 600;
  font-size: 48px;
  line-height: 1.5;
  margin-bottom: 48px;
}
.about__content {
  font-weight: 300;
  font-size: 18px;
  border-radius: 4px;
  line-height: 1.5;
  margin-left: -600px;
  padding-left: 600px;
}
.slide__letter {
  display: inline-block;
  transition: transform 1s ease;
}
.slide-skills__letter {
  display: inline-block;
  transition: transform 1s ease;
}
.slide,
.slide__content {
  opacity: 0;
  -moz-transition: all 1000ms linear;
  -webkit-transition: all 1000ms linear;
  -o-transition: all 1000ms linear;
  transition: all 1000ms linear;
  -moz-transform: translate3d(-200px, 0px, 0px);
  -webkit-transform: translate3d(-200px, 0px, 0px);
  -o-transform: translate(-200px, 0px);
  -ms-transform: translate(-200px, 0px);
  transform: translate3d(-200px, 0px, 0px);
}
.slide__content {
  opacity: 0;
  -moz-transition: all 1000ms linear;
  -webkit-transition: all 1000ms linear;
  -o-transition: all 1000ms linear;
  transition: all 1000ms linear;
  -moz-transform: translate3d(200px, 0px, 0px);
  -webkit-transform: translate3d(200px, 0px, 0px);
  -o-transform: translate(200px, 0px);
  -ms-transform: translate(200px, 0px);
  transform: translate3d(200px, 0px, 0px);
}
.hasSlid {
  opacity: 1;
  -moz-transform: translate3d(0px, 0px, 0px);
  -webkit-transform: translate3d(0px, 0px, 0px);
  -o-transform: translate(0px, 0px);
  -ms-transform: translate(0px, 0px);
  transform: translate3d(0px, 0px, 0px);
}
.card--alt--hover {
  background: linear-gradient(
    to right,
    white  0,
    white 200px,
    #F5FCFF 200px,
    #F5FCFF 400px,
    #DBF3FA 400px,
    #DBF3FA 600px,
    white 600px,
    white 800px,
    #F5FCFF 800px,
    #F5FCFF 1000px,
    #DBF3FA  1000px,
    #DBF3FA 1200px,
    white 1200px,
    white 1400px,
    #F5FCFF 1400px,
    #F5FCFF 1600px

  );
  animation: cta-background 10s linear infinite;
}
@keyframes cta-background {
  /*400px takes into account max-widht of 320px plus 80px of padding*/
  to {
    background-position: -400px 0;
  }
}
@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}
